import { Component, AfterViewInit, Input, Output, EventEmitter } from '@angular/core';
import { BcFooterBehavior } from '@bancolombia/design-system-behaviors';
import { BcHelperService } from '@bancolombia/design-system-web/bc-helpers';

interface ITopLinks {
  /** asas */
  alignContent: 'flex-end' | 'flex-start' | 'center';
  links?: Array<{
    text: string;
    link?: string;
    method?: string;
    isSelf?: boolean;
  }>;
}

interface ITopMethods {
  alignContent: 'flex-end' | 'flex-start' | 'center';
  methods: Array<{
    text: string;
    method: string;
  }>;
}

interface ITopTextContent {
  title: {
    text: string;
    link: string;
    onlyStyleText: boolean;
    isSelf: boolean;
  };
  texts: Array<{ text: string; link: string; isSelf: boolean }>;
}

interface ISocialNetBottomRight {
  name: string;
  link: string;
  isSelf: boolean;
}

interface IColorsStroke {
  color1: string;
  color2: string;
  color3: string;
  color4: string;
  color5: string;
  color6: string;
}

/**
 * Importa el componente footer con los diseños de Bancolombia S.A. este componente,
 * se utiliza para los pie de página.
 *
 * @export
 * @class BcfooterComponent
 * @implements {AfterViewInit}
 */
@Component({
  selector: 'bc-footer',
  templateUrl: 'bc-footer.component.html',
})
export class BcFooterComponent implements AfterViewInit {
  /**
   * Id del elemento.
   *
   * @type {string}
   * @memberof BcfooterComponent
   */
  @Input() componentId: string = this.bcHelperService.getId(this);

  /**
   * Links con alineacion en el top del footer, los posibles valores de align (alineación) son "flex-end" | "flex-start" | "center".
   *
   * @type {ITopLinks}
   * @memberof BcfooterComponent
   */
  @Input() topLinks: ITopLinks = {
    alignContent: 'flex-end',
    links: [
      {
        text: '',
        method: '',
        link: '',
        isSelf: false,
      },
    ],
  };

  @Input() topMethods: ITopMethods = {
    alignContent: 'flex-end',
    methods: [
      {
        text: '',
        method: '',
      },
    ],
  };

  /**
   * Objeto para mostrar los textos de la parte superior del footer.
   *
   * @type {ITopTextContent[]}
   * @memberof BcfooterComponent
   */
  @Input() topTextContent: ITopTextContent[] = [
    {
      title: { text: '', link: '', onlyStyleText: false, isSelf: false },
      texts: [{ text: '', link: '', isSelf: false }],
    },
  ];

  /**
   * Array de textos que se muestra en la parte inferior izquierda del footer.
   *
   * @type {Array<string>}
   * @memberof BcfooterComponent
   */
  @Input() bottomTextLeftContent: Array<string> = [];
  /**
   * Array de textos que se muestra en la parte inferior derecha del footer.
   *
   * @type {Array<string>}
   * @memberof BcfooterComponent
   */
  @Input() bottomTextRighContent: Array<string> = [];
  /**
   * Array de objetos para las redes sociales. En name pueden ir facebook,twitter,linkedin,instagram,youtube.
   *
   * @type {ISocialNetBottomRight[]}
   * @memberof BcfooterComponent
   */
  @Input() socialNetBottomRight: ISocialNetBottomRight[] = [{ name: '', link: '', isSelf: false }];
  /**
   * Boolean que por defecto esta true y muestra el icono bancolombia en la parte inferior.
   *
   * @type {boolean}
   * @memberof BcfooterComponent
   */
  @Input() isBancoLogoBottom = false;
  /**
   * Boolean que por defecto esta true y muestra el icono vigilado en la parte inferior.
   *
   * @type {boolean}
   * @memberof BcfooterComponent
   */
  @Input() isVigilaLogoBottom = true;
  /**
   * Boolean que por defecto esta en false y cambia el background del footer a blanco.
   *
   * @type {boolean}
   * @memberof BcfooterComponent
   */
  @Input() bgWhite = false;

  /**
   * Define el tipo de color de fondo del footersi esta en true cambiara a gris.
   *
   * @type {boolean}
   * @memberof BcfooterComponent
   */
  @Input() bgGray = false;

  /**
   * String el cual nos permite alinear el logo de bancolombia a la izquierda( el que se encuentra cerca de los copyright).
   *
   * @type {string}
   * @memberof BcfooterComponent
   */
  @Input() icon: 'left' | 'top' = 'left';

  /**
   * Define el logo a usar en el footer.
   *
   * @type {string}
   * @memberof BcfooterComponent
   */
  @Input() logo: string = 'isotipo-bancolombia';

  /**
   * Objeto que permite cambiar los colores del trazo,
   * el color stroke va desde 1 hasta el 6.
   * @type {IColorsStroke}
   * @memberof BcfooterComponent
   */
  @Input() colorStroke: IColorsStroke;
  

  @Output() methodCallout: EventEmitter<string> = new EventEmitter<string>();

  bcFooterBehavior;

  constructor(private bcHelperService: BcHelperService) {
    this.bcFooterBehavior = new BcFooterBehavior();
  }

  methodCall(method: string) {
    this.methodCallout.emit(method);
  }

  ngAfterViewInit() {
    this.bcFooterBehavior.setId(this.componentId);
    this.bcFooterBehavior.setUp();
    this.bcFooterBehavior.createParameterizationStrokeColors(this.colorStroke);
  }
}
